import * as React from 'react'
import * as ReactDOM from 'react-dom'
import * as queryString from 'query-string'
import { fetchPrice } from '../PriceApi'
import { roundEthValue } from '../utils'
declare var web3 : any

interface State {
  total: number
  count: number
  address: string
  title?: string
  price?: any
}

export default class CreateRoot extends React.Component<{}, State> {
  constructor (props: {}) {
    super(props)

    this.state = {
      total: 6000,
      count: 4,
      address: this.defaultAccount() || ''
    }
  }

  defaultAccount () {
    return web3 && web3.currentProvider && web3.eth.accounts[0]
  }

  componentDidMount () {
    fetchPrice().then((price: any) => {
      this.setState({ price })
    })
  }

  totalOnChange(e: any) {
    this.setState({ total: e.target.value })
  }

  countOnChange (e: any) {
    this.setState({ count: e.target.value })
  }

  addressOnChange (e: any) {
    this.setState({ address: e.target.value })
  }

  titleOnChange (e: any) {
    this.setState({ title: e.target.value })
  }

  amount () {
    return Math.ceil(this.state.total / this.state.count)
  }

  amountInEth () {
    return this.state.price && (this.amount() / this.state.price.JPY)
  }


  buildPayUrl () {
    const query = {
      address: this.state.address,
      amount: '' + this.amountInEth(),
      title: this.state.title
    }

    return `${window.location.pathname}?${queryString.stringify(query)}`
  }

  redirectToPayPage () {
    window.location.href = this.buildPayUrl()
  }

  inputsAreValid () {
    return this.amountInEth() > 0 && window.web3.isAddress(this.state.address)
  }

  render () {
    return (
      <div className='container'>
        <h1></h1>
        <p>Ethereum</p>

        <div className='mb-4'>
          <label> ()</label>

          <input
            className='form-control'
            type='text'
            placeholder=''
            value={ this.state.title || '' }
            onChange={ this.titleOnChange.bind(this) } />
        </div>

        <div className='mb-4'>
          <label> ()</label>
          <input className='form-control' type='number' min={0} value={ this.state.total } onChange={ this.totalOnChange.bind(this) } />
        </div>

        <div className='mb-4'>
          <label></label>
          <input className='form-control' type='number' min={0} value={ this.state.count } onChange={ this.countOnChange.bind(this) } />
        </div>

        <section>
          <h2>{ this.amount() } = { roundEthValue(this.amountInEth()) } ETH</h2>
          <p>1  (: { this.state.price && Math.round(this.state.price.JPY) }  / ETH) </p>
        </section>

        <div className='mb-4'>
          <label></label>
          <input className='form-control' type="text" placeholder='0x1234567890...' value={ this.state.address } onChange={this.addressOnChange.bind(this)} />
        </div>

        <button
          className='btn btn-primary btn-lg btn-block'
          disabled={!this.inputsAreValid()}
          onClick={ this.redirectToPayPage.bind(this) }></button>
      </div>
    )
  }
}



